.rotate {
  -moz-animation: rotate 2s linear infinite;
  -webkit-animation: rotate 2s linear infinite;
  animation: rotate 2s linear infinite;
}

.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@media (prefers-reduced-motion: no-preference) {
  .grid-entry-header .entry-title,
  .entry-header:not(.grid-entry-header) {
    animation: start-actions 0.75s ease;
    animation-fill-mode: forwards;
    animation-delay: 0.25s;
    opacity: 0;
    transform: translateY(1rem);
    transform-origin: bottom left;
    border-bottom-color: transparent;
  }
  .grid-entry-header .entry-meta {
    animation: start-actions 0.75s ease;
    animation-fill-mode: forwards;
    animation-delay: 0.3s;
    opacity: 0;
    transform: translateY(1rem);
    transform-origin: bottom left;
  }
  .entry-content {
    animation: start-summary 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation-delay: 0.35s;
  }
  .page-title,
  .archive-description {
    animation: start-title 1s ease;
    animation-fill-mode: forwards;
    opacity: 0;
    transform: scaleY(0);
    transform: translateY(-0.25em);
  }
  .site-header {
    animation: fadeInDown 0.25s ease-out;
    animation-fill-mode: both;
    opacity: 0;
    -webkit-transform: translate3d(0, 0, -30px);
    transform: translate3d(0, 0, -30px);
  }
}
@-moz-keyframes rotate {
  from {
    -moz-transform: rotate(0deg);
  }
  to {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes start-actions {
  0% {
    opacity: 0;
    transform: translateY(0.3rem);
    border-bottom-color: transparent;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    border-bottom-color: var(--bs-primary);
  }
}
@keyframes start-title {
  0% {
    opacity: 0;
    transform: translateY(0.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes start-summary {
  0% {
    opacity: 0;
    transform: translateY(-0.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

