
// Headings 


h1, .h1,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6{
    letter-spacing: -.25pt;
}


// Font sizes

.site{

    .has-small-font-size {
        font-size: 16px; 
    }
    
    .has-regular-font-size,
    .has-normal-font-size {
        font-size: 18px; 
    }
    
    .has-medium-font-size {
        font-size: 22px;
    }
    
    .has-large-font-size {
        font-size: 28px;
        line-height: 1.4;
    }

    .has-large-font-size {
        font-size: 36px;
        line-height: 1.3;
    }

    .has-huge-font-size {
        font-size: 42px; 
        line-height: 1.25;
    }

}


// Weights

b, strong {
    font-weight: $font-weight-bold;
}

// Quotes

blockquote {
	font-size: $font-size-lg;
	font-weight: inherit;
    margin: $block-margin-y 0;
    position: relative;
    padding: 0 3rem;

	&::before {
        content: "\201C";
        color: var(--#{$prefix}border-color);
        line-height: 0.9;
        font-size: 6rem;
        line-height: .8;
        position: absolute;
        left: 0;
        top: 0;
    }

    p{
        margin-bottom: 1rem;
    }

    cite{
        font-size: $font-size-sm;
    }
}

.entry-header .entry-title{
    margin-bottom: .5rem;
}

a{
    text-underline-offset: 2px;
}

.entry-content{
    font-size: $font-size-lg;
}

.layout-grid .entry-content{
	font-size: $font-size-base;
}

.layout-grid .no-results .entry-content{
    font-size: $font-size-lg;
}


.entry-content a:not(.btn):not(.wp-element-button):not(.wp-block-button__link):not(.wp-block-social-link-anchor):not(.wp-block-file__button){
    text-decoration: underline;
}

.entry-content .pagination a{
    text-decoration: none!important;
}